<template>
  <v-content>
    <v-menu :nudge-width="400" offset-y v-model="settings" :close-on-content-click="false">
      <template v-slot:activator="{ on }">
        <v-btn v-on="on" :large="true" :text="true" icon>
          <v-icon large color="black">settings</v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-row justify="center" style="margin:0">
          <h2>{{$t("settings.title")}}</h2>
        </v-row>
        <v-divider></v-divider>
        <v-row style="margin:0" align="center" justify="center">
          <v-col>
            <v-row justify="center">{{$t("settings.system")}}</v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col>
            <v-select
              v-model="user.systemType"
              :items="types.system"
              @change="setSystemType"
              item-text="name"
              item-value="id"
              :full-width="true"
              filled
              disabled
              dense
              rounded
              class="fieldStyle"
            ></v-select>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row style="margin:0" align="center" justify="center">
          <v-col>
            <v-row justify="center">{{$t("settings.currency")}}</v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col>
            <v-select
              v-model="user.currency"
              :items="types.currency"
              @change="setCurrency"
              item-text="name"
              item-value="id"
              :full-width="true"
              filled
              disabled
              dense
              rounded
              class="fieldStyle"
            ></v-select>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row style="margin:0" align="center" justify="center">
          <v-col>
            <v-row justify="center">{{$t("settings.language")}}</v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col>
            <v-select
              v-model="$i18n.locale"
              :items="languages"
              :full-width="true"
              item-text="name"
              item-value="locale"
              filled
              dense
              rounded
              class="fieldStyle"
              @change="setCookie"
            ></v-select>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row style="margin:0;background-color:#F7E159;" align="center" justify="center">
          <v-col>
            <v-row justify="center">{{$t("settings.program")}}</v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col>
            <v-row
              justify="center"
            >{{values.program.find(el => el.id == user.program).name[$i18n.locale]}}</v-row>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row style="margin:0" align="center" justify="center">
          <v-btn @click="logout" block>{{$t("settings.logout")}}</v-btn>
        </v-row>
      </v-card>
    </v-menu>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import Cookies from "js-cookie";

export default {
  name: "settingsClient",
  data() {
    return {
      settings: false
    };
  },
  computed: mapGetters(["types", "values", "user", "languages"]),
  methods: {
    setCookie: function() {
      Cookies.set("language", this.$i18n.locale);
    },
    logout: function() {
      this.$store.dispatch("logout");
    },
    setSystemType: async function(type) {
      let data = {
        systemType: type
      };
      this.$store.dispatch("updateUser", data);
      this.$router.go();
    },
    setCurrency: async function(curr) {
      let data = {
        currency: curr
      };
      this.$store.dispatch("updateUser", data);
      this.$router.go();
    }
  }
};
</script>