<template>
  <v-content>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <howYouFeelClient :show="showReview" @closed="closeReview"></howYouFeelClient>

    <div v-if="overlay === false">
      <v-row align="center">
        <v-col style="padding:12px 0px 5px 20px" cols="auto">
          <v-avatar size="80">
            <img :src="user.avatar" />
          </v-avatar>
        </v-col>
        <v-col style="padding:12px 3px 3px 1%" cols="5">
          <v-row align="center" justify="center" no-gutters>
            <h1>{{$t("titleClient")}} {{user.firstName}}!</h1>

            <v-col style="padding:0 0 0 5%">
              <settings></settings>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row align="start" justify="center" no-gutters>
        <v-col>
          <calendar></calendar>
        </v-col>

        <v-col cols="4">
          <charts></charts>
        </v-col>
      </v-row>
    </div>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import calendar from "../components/calendar";
import charts from "../components/charts";
import settings from "../components/settingsClient";
import howYouFeelClient from "../components/howYouFeelClient";
import moment from "moment";

export default {
  name: "Client",
  components: { calendar, charts, settings, howYouFeelClient },
  data: () => ({
    openTrainerSignUp: false,
    openClientSignUp: false,
    overlay: false,
    clientData: false,
    showReview: false,
    clientsArray: []
  }),
  computed: mapGetters(["user", "clients", "types", "values", "rate"]),
  async created() {
    this.overlay = true;
    await this.$store.dispatch("getUser");
    await this.$store.dispatch("types");
    await this.$store.dispatch("values");
    await this.$store.dispatch("getRoleInfo");
    await this.$store.dispatch("checkRate", moment().format("YYYY-MM-DD"));
    this.overlay = false;
    if ((moment().day() == 0 || moment().day() == 1) && !this.rate) {
      this.showReview = true;
    }
  },
  methods: {
    clientCalendar(data) {
      console.log("ActiveClient");
      this.$store.commit("activeClient", data);
    },
    buttonPress() {
      console.log("button press");
    },
    closeReview() {
      this.showReview = false;
    }
  }
};
</script>

<style>
.overflowText {
  display: inline-block;
  width: 120px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.client:hover {
  cursor: pointer;
  background: #f5f5f5;
  transition: 0.5s cubic-bezier(0.25, 0.8, 0.5, 1);
}

.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>