<template>
  <v-content>
    <v-snackbar v-model="show" color="info" :timeout="timeout" top>
      <v-col>
        <v-row justify="center" align="center">
          <h3>How do you feed this week</h3>
        </v-row>
        <v-row justify="center" align="center">
          <v-rating
            v-model="rating"
            @input="uploadRate"
            color="yellow darken-3"
            background-color="grey darken-1"
            empty-icon="$ratingFull"
            half-increments
            hover
          ></v-rating>
        </v-row>
        <v-row align="center" justify="end">
          <v-btn dark text @click="closeAlert">Close</v-btn>
        </v-row>
      </v-col>
    </v-snackbar>
  </v-content>
</template>

<script>
import moment from "moment";

export default {
  name: "Review",
  props: ["show"],
  data() {
    return {
      timeout: 60000,
      rating: 0
    };
  },
  methods: {
    uploadRate() {
      this.$store.dispatch("uploadRate", {
        rate: this.rating,
        date: moment().format("YYYY-MM-DD")
      });
      this.closeAlert();
    },
    closeAlert() {
      this.$emit("closed");
    }
  }
};
</script>